import axios from "axios";
import { useEffect, useState } from "react";
import styles from "./Products.module.css";
import {Link} from "react-router-dom";
const Products=() =>{
  const [products, setProducts] = useState([]);
  useEffect(() => {
    axios
      .get("https://5d76bf96515d1a0014085cf9.mockapi.io/product")
      .then((res) => {
        setProducts(res.data);
      })
      .catch((err) => console.log(err));
  }, []);
  return (
    <div>
      <div className="card-deck d-flex flex-wrap products">
        {products.length==0?<h1>Loading...</h1>:products.map((item, idx) => {
          return (
            <Link to={`/Products/${item.id}`}>
            <div className="card product" style={{
              width:"18rem"
            }}>
              <img className="card-img-top" src={item.preview} alt="Card image cap"/>
              <div className="card-body">
                <h5 className="card-title">{item.name}</h5>
                <p className="card-para card-text">Some quick example text to build on the card title and make up the bulk of the card's content. Some quick example text to build on the card title and make up the bulk of the card's content</p>
                <Link to={`/Products/${item.id}`} className="btn btn-primary">Get Details</Link>
              </div>
            </div>
            </Link>
            );
        })}
      </div>
    </div>
  );
}

export default Products;